import { useFormik } from "formik";
import * as yup from "yup";
import Form from "./Form";
import { useCallback, useMemo } from "react";
import { createPostRequest } from "../../../../utility/services/requests";
import ErrorPopup   from "../../../customizedComponents/ErrorPopup";
 import { Modal, ModalBody, ModalHeader } from "reactstrap";
import { X } from "react-feather";



const Add = ({ t, handleModal, open, forceUpdate }) => {
  const title = t("NewAdd");
  const toggleModal = useCallback(() => {
    formik.resetForm();
    handleModal();
  }, [handleModal]);
  const validationSchema = useMemo(
    () =>
      yup.object().shape({
        id: yup.mixed().nullable(), 
        firstName: yup.string().required(`${t("RequiredAddName")}`),
        lastName: yup.string().required(`${t("RequiredLastName")}`),
        // photo: yup.mixed().nullable(),
        ownerSiteId: yup.string().nullable(),
        userName: yup.string().required(`${t("RequiredUserName")}`),
        normalizedUserName: yup.string().nullable(),
        email: yup.string().email(`${t("InvalidEmail")}`).required(`${t("RequiredEmail")}`),
        normalizedEmail: yup.string().nullable(),
        emailConfirmed: yup.boolean().required(`${t("RequiredEmailConfirmed")}`),
        phoneNumber: yup.string()
          .nullable()
          .matches(/^\d+$/, `${t("InvalidPhoneNumber")}`), // التأكد من أنه رقم فقط
        phoneNumberConfirmed: yup.boolean().required(`${t("RequiredPhoneConfirmed")}`),
        active: yup.boolean().required(`${t("RequiredActive")}`),
      }),
    [t]
  );
  
    
    const formik = useFormik  ({
      initialValues: {
  id:null,
  firstName:null,
  lastName:null,
  photo:null,
  ownerSiteId:null,
  userName:null,
  normalizedUserName:null,
  email:null,
  normalizedEmail:null,
  emailConfirmed:false,
  phoneNumber:null,
  phoneNumberConfirmed:false,
  active:true,
 
      },
      validationSchema,
      onSubmit: (values) => {
        createPostRequest({ endPoint: "Haj/AspNetUsers/Post" })({
          body: values,
          onSuccess: () => {
            toggleModal();
            forceUpdate();
            formik.resetForm();
          },
          onFailure: () => {
            ErrorPopup("تعذر الإضافة");
          },
        });
      },
  });
  
  const CloseBtn = useMemo(
    () => (
      <div style={{ display: "inline-block" }}>
        <X style={{ marginLeft: "20px" }} className="cursor-pointer" size={20} onClick={toggleModal} />
      </div>
    ),
    [toggleModal]
  );

  return (
    <>
      <Modal isOpen={open} toggle={toggleModal} style={{ maxWidth: "80%" }}>
        <input hidden name="screenId" defaultValue="city_ADD_MODAL_01" />
        <ModalHeader className="mb-3" toggle={toggleModal} close={CloseBtn} tag="div">
          <h3 className="modal-title">{title}</h3>
        </ModalHeader>
        <ModalBody className="flex-grow-1">
          <Form formik={formik} typeForm="add" />
        </ModalBody>
      </Modal>
    </>
  );
};

export default Add;
