import { useState, useCallback, useMemo } from "react";
import { createDeleteRequest, createGetRequest } from "../../../../utility/services/requests";
import PageTitle from "../../../customizedComponents/PageTitle";
import BreadcrumbsDefault from "../../../customizedComponents/breadcrumbs";
import { ConfirmCancelPopup, deleteIconColor, editIconColor,showIconColor, Icons } from "../../../customizedComponents/generic_page";
import ServerSideTable from "../../../customizedComponents/ServerSideTable";
 import Add from "./Add";
import Edit from "./Edit";
 import Show from "./Show";
 import { useTranslation } from "@hooks/useTranslation";

 

export default function List() {
  const [store, setStore] = useState([]);
  const [rowID, setRowID] = useState([]);
  const [addModal, setAddModal] = useState(false);
  const [updateModal, setUpdateModal] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [key, setKey] = useState(1);
  const [isLoading, setIsLoading] = useState(true);
  const { t } = useTranslation();  
  const title = t("Users");
  const breadcrumbs = [
    {
      link: null,
     name: title,
   },
 ];;

   const forceUpdate = useCallback(() => {
    setKey((prevKey) => prevKey + 1);
  }, []);

   const handleModal = useCallback(() => {
    setAddModal((prev) => !prev);
  }, []);

  const handleUpdateModal = useCallback((id) => {
    setRowID(id)
    setUpdateModal((prev) => !prev);
  }, []);
  const handleShowModal = useCallback((id) => {
    setRowID(id)
    setShowModal((prev) => !prev);
  }, []);
   const handleDelete = useCallback(
    (id) => {
      ConfirmCancelPopup({message:t("MessageDelete")}).then((out) => {
        if (out.isConfirmed) {
          createDeleteRequest({ endPoint: "Haj/AspNetUsers/Delete/" })({
            id: id,
            onSuccess: forceUpdate,
          });
        }
      });
    },
    [forceUpdate]
  );
 
   const columns = useMemo(
    () => [
      {
        name: ("firstName"),
        selector:(row) =>row?.firstName ,
      },
      {
        name: ("lastName"),
        selector: (row) =>row?.lastName,
      },
      {
        cell: (row) => (
          <div>
            <>
               <Icons.TbEye color={showIconColor} size={25} className="mr-50 cursor-pointer" onClick={() => handleShowModal(row?.id)} />
            </>
            <>
               <Icons.TbEditCircle color={editIconColor} size={25} className="mr-50 cursor-pointer" onClick={() => handleUpdateModal(row?.id)} />
            </>
             <Icons.MdDeleteForever
              color={deleteIconColor}
              size={25}
              className="mr-50 cursor-pointer"
              onClick={() => handleDelete(row.id)}
            />
          </div>
        ),
        minWidth: "150px",
      },
    ],
    [handleDelete,t]
  );

   const fetchStore = useCallback(
    (PageNumber = 1, PageSize = 5, PageSearch = "") => {
      setIsLoading(true);
      createGetRequest({ endPoint: "Haj/AspNetUsers/GetAll" })({
        params: { PageNumber: PageNumber , PageSize, PageSearch: PageSearch.trimEnd() },
        onSuccess: (data) => {
          setStore(data);
          setIsLoading(false);
        },
      });
    },
    []
  );
 
  return (
    <>
      <PageTitle title={title} />
      <BreadcrumbsDefault list={breadcrumbs} />
      <ServerSideTable
        refresherKey={key}
        columns={columns}
        data={store?.data}
        totalRecords={store?.totalRecords}
        isLoading={isLoading}
        onPagination={fetchStore}
        handleAddButton={handleModal}
        enableAdd={true}
        enableSearch={true}
        enableExtraction={false}
      />
      <Add open={addModal} handleModal={handleModal} forceUpdate={forceUpdate} t={t} />
      <Edit open={updateModal} handleUpdateModal={handleUpdateModal} forceUpdate={forceUpdate} id={rowID} t={t} />
      <Show open={showModal} handleShowModal={handleShowModal} forceUpdate={forceUpdate} id={rowID} t={t} />

    </>
  );
}
