import { Card, CardBody, Col, FormGroup, Input, Label, Row, Button, CustomInput } from "reactstrap";
  import { useTranslation } from "@hooks/useTranslation";
import SelectScroll from "../../../customizedComponents/SelectScroll";
 
export default function ({ formik, typeForm  }) {
  const { t } = useTranslation();
  return (
    <>
      <Card>
        <CardBody>
        <Row>
            <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="firstName">{t("firstName")}</Label>
                <Input
                  type="text"
                  placeholder={t("firstName")}
                  autoComplete="off"
                  {...formik.getFieldProps('firstName')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.firstName && formik.errors.firstName ? (
                  <small className="text-danger">{formik.errors.firstName}</small>
                ) : null}
              </FormGroup>
             </Col>  
            <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="lastName">{t("lastName")}</Label>
                <Input
                  type="text"
                  placeholder={t("lastName")}
                  autoComplete="off"
                  {...formik.getFieldProps('lastName')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.lastName && formik.errors.lastName ? (
                  <small className="text-danger">{formik.errors.lastName}</small>
                ) : null}
              </FormGroup>
             </Col>  
            <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="userName">{t("userName")}</Label>
                <Input
                  type="text"
                  placeholder={t("userName")}
                  autoComplete="off"
                  {...formik.getFieldProps('userName')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.userName && formik.errors.userName ? (
                  <small className="text-danger">{formik.errors.userName}</small>
                ) : null}
              </FormGroup>
             </Col>  
            <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="normalizedUserName">{t("normalizedUserName")}</Label>
                <Input
                  type="text"
                  placeholder={t("normalizedUserName")}
                  autoComplete="off"
                  {...formik.getFieldProps('normalizedUserName')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.normalizedUserName && formik.errors.normalizedUserName ? (
                  <small className="text-danger">{formik.errors.normalizedUserName}</small>
                ) : null}
              </FormGroup>
             </Col>  
            <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="email">{t("email")}</Label>
                <Input
                  type="text"
                  placeholder={t("email")}
                  autoComplete="off"
                  {...formik.getFieldProps('email')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.email && formik.errors.email ? (
                  <small className="text-danger">{formik.errors.email}</small>
                ) : null}
              </FormGroup>
             </Col>  
            <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="normalizedEmail">{t("normalizedEmail")}</Label>
                <Input
                  type="text"
                  placeholder={t("normalizedEmail")}
                  autoComplete="off"
                  {...formik.getFieldProps('normalizedEmail')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.normalizedEmail && formik.errors.normalizedEmail ? (
                  <small className="text-danger">{formik.errors.normalizedEmail}</small>
                ) : null}
              </FormGroup>
             </Col>  
             <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="emailConfirmed">{t("emailConfirmed")}</Label>
                <CustomInput
                  type="switch"
                  id="emailConfirmed"
                  name="emailConfirmed"
                  autoComplete="off"
                  checked={formik.values?.emailConfirmed}
                  onChange={formik.handleChange}
                  disabled={typeForm === "show"}
                />
              </FormGroup>
            </Col>
            <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="password">{t("password")}</Label>
                <Input
                  type="text"
                  placeholder={t("password")}
                  autoComplete="off"
                  {...formik.getFieldProps('password')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.password && formik.errors.password ? (
                  <small className="text-danger">{formik.errors.password}</small>
                ) : null}
              </FormGroup>
             </Col>  
             <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="confirmPassword">{t("confirmPassword")}</Label>
                <Input
                  type="text"
                  placeholder={t("confirmPassword")}
                  autoComplete="off"
                  {...formik.getFieldProps('confirmPassword')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.confirmPassword && formik.errors.confirmPassword ? (
                  <small className="text-danger">{formik.errors.confirmPassword}</small>
                ) : null}
              </FormGroup>
             </Col>  
            
             <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="phoneNumber">{t("phoneNumber")}</Label>
                <Input
                  type="text"
                  placeholder={t("phoneNumber")}
                  autoComplete="off"
                  {...formik.getFieldProps('phoneNumber')}
                  disabled={typeForm=="show"}
                />
                 {formik.touched.phoneNumber && formik.errors.phoneNumber ? (
                  <small className="text-danger">{formik.errors.phoneNumber}</small>
                ) : null}
              </FormGroup>
             </Col>  
           
           
             <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="phoneNumberConfirmed">{t("phoneNumberConfirmed")}</Label>
                <CustomInput
                  type="switch"
                  id="phoneNumberConfirmed"
                  name="phoneNumberConfirmed"
                  autoComplete="off"
                  checked={formik.values?.phoneNumberConfirmed}
                  onChange={formik.handleChange}
                  disabled={typeForm === "show"}
                />
              </FormGroup>
            </Col>
             {/* <Col sm={12} md={6} lg={3}>
              <FormGroup>
                <Label for="active">{t("active")}</Label>
                <CustomInput
                  type="switch"
                  id="activeSwitch"
                  name="active"
                  autoComplete="off"
                  checked={formik.values?.active}
                  onChange={formik.handleChange}
                  disabled={typeForm === "show"}
                />
              </FormGroup>
            </Col> */}
 </Row>
          <Row style={{textAlign:"center" ,justifyContent:"center"}}>
            
<Col sm={6}>
  <Button color="primary" onClick={() => {formik.submitForm()}}>
    {typeForm=="add"?t("Add"):typeForm=="edit"?t("Edit"):t("Cancel")}
  </Button>
</Col>
</Row>
        </CardBody>
      </Card>
    </>
  );
}
