import { useFormik } from "formik";
import * as yup from "yup";
import Form from "./Form";
import { useState, useCallback, useMemo, useEffect } from "react";
import { createGetRequest } from "../../../../utility/services/requests";
  import { Modal, ModalBody, ModalHeader } from "reactstrap";
import { X } from "react-feather";
const Show = ({ t, handleShowModal, open, forceUpdate, id }) => {
  const TITLE = t("Show");
  const [initialValues, setInitialValues] = useState({
    name: "",
    active: true,
  });
  
  const toggleModal = useCallback(() => {
    formik.resetForm();
    handleShowModal();
  }, [handleShowModal]);
  
  const validationSchema = useMemo(
    () =>
      yup.object().shape({
        name: yup.string().required(`${t("RequiredAddName")}`),
      }),
      [t]
    );
    
    useEffect(() => {
      if (open) {
        createGetRequest({ endPoint: `Haj/AspNetUsers/GetById/${id}` })({
          onSuccess: (response) => {
            setInitialValues(response);
          },
        });
      }
    }, [open, id]);
    
    const formik = useFormik({
      initialValues,
      enableReinitialize: true, // تمكين إعادة التهيئة
      validationSchema,
      onSubmit: () => { 
      toggleModal()
     },
  });

  const CloseBtn = useMemo(
    () => (
      <div style={{ display: "inline-block" }}>
        <X style={{ marginLeft: "20px" }} className="cursor-pointer" size={20} onClick={toggleModal} />
      </div>
    ),
    [toggleModal]
  );

  return (
    <>
      <Modal isOpen={open} toggle={toggleModal} style={{ maxWidth: "80%" }}>
        <input hidden name="NameId" defaultValue="Type_Edit_MODAL_01" />
        <ModalHeader className="mb-3" toggle={toggleModal} close={CloseBtn} tag="div">
          <h3 className="modal-title"> {TITLE}      </h3>
        </ModalHeader>
        <ModalBody className="flex-grow-1">
          <Form formik={formik} typeForm="show" />
        </ModalBody>
      </Modal>
    </>
  );
};

export default Show;
